
export default {
  bind(el) {
      const dialogHeaderEl = el.lastChild; // DOM
      dialogHeaderEl.style.cursor = 'move';
      const dragDom = el; // DOM
      // //  ie dom.currentStyle  window.getComputedStyle(dom, null);
      // const sty = dragDom.currentStyle || window.getComputedStyle(dragDom, null);
      dialogHeaderEl.onmousedown = (e) => {
          const oEvent = e || event;
          // 
          dragDom.style.transition = 'none';
          document.onmousemove = function (e) {
          //  
              const l = e.clientX  - 50;
              const t = e.clientY  - 30;
              let finallyL = l;
              let finallyT = t;
              // top 
              if (finallyT < 0) {
                  finallyT = 0;
              }
              // left 
              if (finallyL < 0) {
                  finallyL = 0;
              }
              // right 
              if (finallyL > 0 && finallyL > parseInt(document.body.clientWidth - dragDom.clientWidth)) {
                  finallyL = parseInt(document.body.clientWidth - dragDom.clientWidth);
              }
              // 
              dragDom.style.left = `${finallyL}px`;
              dragDom.style.top = `${finallyT}px`;

          // 
          // binding.value({x:e.pageX,y:e.pageY})
          };
          document.onmouseup = function () {
              document.onmousemove = null;
              document.onmouseup = null;
              // e.preventDefault();
          };
          oEvent.preventDefault();
      };
  }
};
